/* Header Styles */
header {
	background-color: var(--primary);
  }
  
  header .inner-content {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 10px 0;
  }
  
  header a {
	color: var(--white);
	font-size: 1.25rem;
	text-decoration: none;
	transition: all 0.3s ease;
  }
  
  header a:hover {
	opacity: 0.7;
  }
  
  header .brand a {
	font-weight: 700;
	font-size: 1.5rem;
  }
  
  header .nav-links {
	padding: 0;
	margin: 0;
	list-style: none;
	display: flex;
	align-items: center;
  }
  
  header .nav-links li {
	display: inline-block;
	margin-right: 30px;
  }
  
  header .nav-links li:last-child {
	margin-right: 0;
  }